import EventBaseLayer from "../bases/EventBaseLayer";

let AudioInfo = {
    layerType: "Audio Event",
    properties: {
        AudioEvent: {
            type: "audio",
            defaultValue: {
                file: ""
            }
        }
    }
};

/*
* Audio layer with specific payload data.
* */
export default class AudioLayer extends EventBaseLayer {

    static getInfo() {
        return AudioInfo;
    }

    constructor() {
        super();
    }

    static generateEvent(props) {

        let event = {
            name: "play-audio",
            payload: {
                "file" : props.AudioEvent.file
            }
        };

        return event;
    }

    static isValid(generatedEvent) {
        return ((generatedEvent.name === "play-audio") &&
                (generatedEvent.payload.file.length > 0));

    }
}